.AddToNews{
    padding: 50px 30px;
    margin-top: 60px;
}
.input-item{
    width: 70%;
}
.input-item input{
    width: 100%;
    height: 40px;
    padding: 10px;
    border-radius: 10px;
}
.input-item input:focus{
    outline: none;
    border: none;
}
.submit-item{
    text-align: center;
}
@media(max-width:991px){
    .input-item{
        width: 100%;
    }
    .input-item input{
        width:100%
    }
}